/* Variables */

:root {
  --gradient-blue: linear-gradient(88.3deg, #1165A1 0%, #4FA4E1 100%);
  --color-blue: #1165A1;
  --color-blue-low: #196DAA;
  --color-darkcyan: #0484AB;
  --color-blue-75: rgba(17, 101, 161, 0.75);
  --color-dark: #333333;
  --color-dark-gray: #555555;
  --color-dark-gray-low: #727272;
  --color-milk: #FEFEFE;
  --color-white: #FFFFFF;
  --color-white-75: rgba(254, 254, 254, 0.75);
  --color-white-gray: #EDF0F3;
  --color-gray: #E5E5E5;
  --font-roboto: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  --font-gotham: "GothamPro", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* Base */

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
}

/* Animation */

/* Base Classes */

/* Containers */

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.container-full {
  padding-left: 20px;
  padding-right: 20px;
}

/* Hide */

.hide {
  display: none !important;
}

/* Position */

.left {
  text-align: left;
}

.center {
  text-align: center;
}

.right {
  text-align: right;
}

/* Background */

.bg--white {
  background-color: var(--color-white);
}

.bg--white-gray {
  background-color: var(--color-white-gray);
}

.bg--gray {
  background-color: var(--color-gray);
}

.bg--dark {
  background-color: var(--color-dark);
}

.bg--blue {
  background-color: var(--color-blue);
}

/* Padding */

.block--padding {
  padding: 50px 0 50px;
}

/* Links */

.link {
  text-decoration: none;
  transition: ease .3s;
}

.link:hover {
  color: var(--color-orange);
}

/* Title */

.title {
  font-family: var(--font-roboto);
  font-style: normal;
  margin: 0;
}

.title--big {
  font-size: calc(35px + 30 * ((100vw - 320px) / 840));
}

@media screen and (min-width: 1160px) {

  .title--big {
    font-size: 65px;
  }
}

@media screen and (max-width: 320px) {

  .title--big {
    font-size: 35px;
  }
}

.title--large {
  font-size: calc(20px + 20 * ((100vw - 320px) / 840));
}

@media screen and (min-width: 1160px) {

  .title--large {
    font-size: 40px;
  }
}

@media screen and (max-width: 320px) {

  .title--large {
    font-size: 20px;
  }
}

.title--medium {
  font-size: calc(18px + 4 * ((100vw - 320px) / 840));
}

@media screen and (min-width: 1160px) {

  .title--medium {
    font-size: 22px;
  }
}

@media screen and (max-width: 320px) {

  .title--medium {
    font-size: 18px;
  }
}

.title--small {
  font-size: 16px;
}

.title--tiny {
  font-size: 12px;
}

.title--white {
  color: var(--color-white);
}

.title--dark {
  color: var(--color-dark);
}

.title--w-light {
  font-weight: 300;
}

.title--w-normal {
  font-weight: 400;
}

.title--w-medium {
  font-weight: 500;
}

.title--w-bold {
  font-weight: bold;
}

.title--w-black {
  font-weight: 900;
}

.title--uppercase {
  text-transform: uppercase;
}

.title--indent {
  margin-bottom: 45px;
}

/* Text */

.text {
  font-family: var(--font-gotham);
  font-style: normal;
  margin: 0;
}

.text--blue {
  color: var(--color-blue);
}

.text--blue-75 {
  color: var(--color-blue-75);
}

.text--darkcyan {
  color: var(--color-darkcyan);
}

.text--dark {
  color: var(--color-dark);
}

.text--dark-gray {
  color: var(--color-dark-gray);
}

.text--dark-gray-low {
  color: var(--color-dark-gray-low);
}

.text--white {
  color: var(--color-white);
}

.text--big {
  font-size: calc(18px + 4 * ((100vw - 320px) / 840));
}

@media screen and (min-width: 1160px) {

  .text--big {
    font-size: 22px;
  }
}

@media screen and (max-width: 320px) {

  .text--big {
    font-size: 18px;
  }
}

.text--large {
  font-size: calc(16px + 4 * ((100vw - 320px) / 840));
}

@media screen and (min-width: 1160px) {

  .text--large {
    font-size: 20px;
  }
}

@media screen and (max-width: 320px) {

  .text--large {
    font-size: 16px;
  }
}

.text--normal {
  font-size: calc(14px + 2 * ((100vw - 320px) / 840));
}

@media screen and (min-width: 1160px) {

  .text--normal {
    font-size: 16px;
  }
}

@media screen and (max-width: 320px) {

  .text--normal {
    font-size: 14px;
  }
}

.text--small {
  font-size: 14px;
}

.text--tiny {
  font-size: 10px;
}

.text--w-light {
  font-weight: 300;
}

.text--w-regular {
  font-weight: normal;
}

.text--w-medium {
  font-weight: 500;
}

.text--w-bold {
  font-weight: 700;
}

.text--w-black {
  font-weight: 900;
}

.text--italic {
  font-style: italic;
}

.text--uppercase {
  text-transform: uppercase;
}

/* Blocks */

/* Button */

.button {
  position: relative;
  display: inline-flex;
  cursor: pointer;
  font-style: normal;
  font-weight: bold;
  line-height: 1.15;
  text-decoration: none;
  text-align: center;
  z-index: 10;
  border-radius: 5px;
  transition: .3s ease-out;
  overflow: hidden;
}

.button--blue {
  font-family: var(--font-gotham);
  padding: 15px 45px;
  color: var(--color-white);
  background: var(--gradient-blue);
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.1);
}

.button--white {
  font-family: var(--font-gotham);
  padding: 10px 45px;
  color: var(--color-blue);
  background: var(--color-white);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.05);
}

.button--blue,
.button--white {
  font-size: 18px;
}

.button .button__span {
  position: relative;
  transition: -webkit-transform .2s ease-out;
  transition: transform .2s ease-out;
  transition: transform .2s ease-out, -webkit-transform .2s ease-out;
}

.button .button__span::before {
  content: attr(data-hover-btn);
  position: absolute;
  width: 100%;
  top: 200%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.button:hover > .button__span {
  -webkit-transform: translateY(-200%);
          transform: translateY(-200%);
}

.button::after {
  content: '';
  display: block;
  position: absolute;
  background: transparent;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transition: .3s ease-out;
  z-index: -1;
}

.button--blue:hover {
  color: var(--color-blue);
  background: transparent;
}

.button--blue:hover::after {
  background: var(--color-white);
}

/* Page */

.page {
  position: relative;
  margin: 0;
  font-family: var(--font-roboto);
  font-size: 16px;
  line-height: 1.22;
  background-color: var(--color-white);
  overflow-x: hidden;
}

.page__container {
  max-width: 1920px;
  margin: 0 auto;
}

/* Header-page */

.header-page {
  padding-top: 100px;
}

.header-page__body {
  position: relative;
}

.header-page__wrapp {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  padding-right: 67px;
}

.header-page__heading {
  display: flex;
  flex-direction: column;
  max-width: 690px;
  padding-bottom: 121px;
}

.header-page__wrapper-title {
  position: relative;
  display: flex;
}

.header-page__title {
  text-align: left;
}

.header-page__logo {
  background: var(--color-white);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 15px;
  width: 135px;
  height: 135px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-page__logo-img {
  display: block;
}

.header-page__pic {
  position: absolute;
  left: 0;
  bottom: 0;
}

.header-page__img {
  display: block;
  max-height: 550px;
}

.header-page__subtitle {
  margin-top: 45px;
}

.header-page__action {
  margin-top: 153px;
}

.header-page__icon {
  position: absolute;
  left: 50%;
  bottom: 36px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  cursor: pointer;
  z-index: 10;
}

/* Why-us */

.why-us__cards {
  margin-top: 45px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: minmax(0, 248px) 50px minmax(0, 248px) 50px minmax(0, 248px) 50px minmax(0, 248px);
  grid-template-columns: repeat(4, minmax(0, 248px));
  grid-auto-rows: 1fr;
  grid-gap: 50px;
  width: 100%;
  justify-content: space-between;
}

.why-us__action {
  text-align: center;
  margin-top: 45px;
}

/* Card-features */

.card-features {
  background: var(--color-milk);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.075);
  border-radius: 10px;
  padding: 31px 43px 50px 43px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-features__icon {
  width: 140px;
  height: 140px;
  background: var(--color-blue);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
