/* Main-form */

/* Form */

.form {
  width: 100%;
}

.form.form--page {
  display: flex;
}

.form__field {
  position: relative;
  width: 100%;
}

.form--page .form__field::before {
  content: '';
  position: absolute;
  left: 0;
  width: 40px;
  height: 100%;
  background: var(--color-white);
  border-radius: 5px;
  z-index: 10;
}

.form--page .form__field.form__field--name::before {
  background-image: url("data:image/svg+xml;base64,");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 16px 21px;
}

.form--page .form__field.form__field--phone::before {
  background-image: url("data:image/svg+xml;base64,");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 18px 19px;
}

.form--page .form__field.form__field--email::before {
  background-image: url("data:image/svg+xml;base64,");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 18px 15px;
}

.form--page .form__field ~ .form__field {
  margin-left: 20px;
}

.form--page .form__action.form__field {
  width: auto;
}

.form--page .form__action.form__field::before {
  content: none;
  display: none;
}

.form__input {
  font-family: var(--font-gotham);
  font-weight: 300;
  font-size: 16px;
  padding: 11px 15px 11px 55px;
  background: var(--color-blue-low);
  -webkit-backdrop-filter: blur(25px);
          backdrop-filter: blur(25px);
  color: var(--color-white-75);
  border: 0;
  outline: 0;
  border-radius: 5px;
  width: 100%;
  flex-shrink: 0;
  flex-grow: 1;
}

.form__input::-webkit-input-placeholder {
  color: var(--color-white-75);
}

.form__input:-ms-input-placeholder {
  color: var(--color-white-75);
}

.form__input::placeholder {
  color: var(--color-white-75);
}

.form__submit {
  border: 0;
  outline: 0;
